<template>
  <v-app style="height: 110vh">
    <v-main>
      <v-dialog v-model="disconnected" max-width="500px" persistent>
        <v-card>
          <v-card-title>Connect to Bluetooth LED Controller</v-card-title>
          <v-card-actions>
            <v-btn
              variant="contained-text"
              color="primary"
              @click="connectBluetooth"
              >Connect Bluetooth</v-btn
            ></v-card-actions
          >
        </v-card>
      </v-dialog>
      <GeneratorList :BLE="BLEDevice" v-show="activeTab == 'Generators'" />
      <PaletteList :BLE="BLEDevice" v-show="activeTab == 'Palettes'" />
    </v-main>
    <NavBar @change="(tab) => (activeTab = tab)" />
  </v-app>
</template>


<script>
import GeneratorList from "./components/GeneratorList.vue";
import PaletteList from "./components/PaletteList.vue";
import NavBar from "./components/NavBar.vue";

var enc = new TextEncoder();

export default {
  name: "App",

  components: { GeneratorList, PaletteList, NavBar },
  created() {},
  data: () => ({
    activeTab: "Generators",
    disconnected: true,
    BLEDevice: {
      device: {},
      server: {},
      service: {},
      genChar: {},
      palChar: {},
      async sendGenerator(genString) {
        console.log(enc.encode(genString).byteLength);
        this.genChar.writeValueWithResponse(enc.encode(genString));
      },
      sendPalette(palString) {
        this.palChar.writeValueWithResponse(enc.encode(palString));
      },
    },
    //
  }),
  methods: {
    async connectBluetooth() {
      console.log("Requesting BLE Device");
      this.BLEDevice.device = await navigator.bluetooth.requestDevice({
        filters: [
          {
            name: "NimBLE-Arduino",
            services: ["c17378c5-b652-4e4b-9b53-45222f86e4d6"],
          },
        ],
      });

      console.log("Connecting to GATT server");
      this.BLEDevice.server = await this.BLEDevice.device.gatt.connect();

      console.log("Getting service");
      this.BLEDevice.service = await this.BLEDevice.server.getPrimaryService(
        "c17378c5-b652-4e4b-9b53-45222f86e4d6"
      );

      console.log("Getting Generator characteristic");
      this.BLEDevice.genChar = await this.BLEDevice.service.getCharacteristic(
        0x6eee
      );

      console.log("Getting Palette characteristic");
      this.BLEDevice.palChar = await this.BLEDevice.service.getCharacteristic(
        0x9a11
      );

      this.disconnected = false;
    },
  },
};
</script>